import {Dispatch} from "redux";
import {getFp} from "../Tools/utilits";
import {AppActionsType} from "../Store/redux-store";

export type FingerPrintActionsType = SetFingerPrintAT

type FingerPrintType = {
    visitorId: string
    visitorPlatform: string
    visitorDeviceMemory: number | undefined
    visitorAudio: number | undefined
    visitorScreenResolution: Array<number> | undefined
}
let initialState: FingerPrintType = {
    visitorId: "",
    visitorPlatform: "",
    visitorDeviceMemory: 0,
    visitorAudio: 0,
    visitorScreenResolution: []
}

export const fingerPrintReducer = (state:FingerPrintType = initialState, action: FingerPrintActionsType) => {
    switch (action.type) {
        case SET_DATA: {
            return {
                ...state,
                ...action.payload
            }
        }
        default:
            return state
    }
}
//--------------------------------------SET-FINGER-PRINT-AC-------------------------------
const SET_DATA = "fingerPrintReducer/SET_DATA"
type SetFingerPrintAT = {
    type: typeof SET_DATA
    payload: FingerPrintType
}
export const setDataAC = (payload: FingerPrintType): SetFingerPrintAT => ({
    type: SET_DATA,
    payload: payload
})
//--------------------------------------SET-FINGER-PRINT-TC-------------------------------
export const setFingerPrintTC = () => async (dispatch: Dispatch<AppActionsType>) => {
    let visitor = await getFp()
    let visitorId = visitor.visitorId
    let visitorDeviceMemory = visitor.components.deviceMemory.value
    let visitorAudio = visitor.components.audio.value
    let visitorScreenResolution = visitor.components.screenResolution.value
    let visitorPlatform = window.navigator.platform

    dispatch(setDataAC({visitorId, visitorPlatform, visitorDeviceMemory, visitorAudio, visitorScreenResolution}))
}